import React from "react"
import { Component } from "react"
import formQuestion from "./FormQuestion"

const FormQuestionContainer = (Wrapper) => {
    return class extends Component {
        
        state = {
            value: "",
            isChecked: true
        }

        changeValue = (e) => {
            this.setState({
                value: e.value
            })
        }

        changeCheckbox = () => {
            this.setState(({isChecked}) => {
                return {
                    isChecked: !isChecked
                }
            })
        }
        submitForm = (e) => {
            e.preventDefault()
            if (this.state.isChecked) {
                console.log(e.target)
                // const data = JSON.stringify("hvdsv")
                const form = new FormData(e.target);
                const req = new XMLHttpRequest(form)
                fetch('/server/', {
                    method: 'POST',
                    headers: {
                        "Access-Control-Request-Headers": "*",
                        "Access-Control-Request-Method": "*"
                    },
                    body: req
                }).then(function (res) {
                    return res.text();
                }).then((body)=> {
                   console.log(body)// body can be used to get data from another server
                 });
            }
        }

        render() {
            return <Wrapper inputValue={this.state.value} 
                            submitForm={this.submitForm}
                            changeValue={this.changeValue}
                            checkBox={this.state.isChecked}
                            changeCheckbox={this.changeCheckbox}
                            />
        }
    }
}

export default FormQuestionContainer(formQuestion)